import React from "react";
import Avatar from "@mui/material/Avatar";
import { Button, Card, Menu, MenuItem } from "@mui/material";
import AutorenewIcon from "@mui/icons-material/Autorenew";
import QueryBuilderIcon from "@mui/icons-material/QueryBuilder";
import WidgetsIcon from "@mui/icons-material/Widgets";
import "./MainLayout.css";
import BTNComp from "../BTNComp";

export default function MainLayoutPage() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <div style={{ margin: "4vh" }}>
      <div className="heading-design">
        <div>
          <img
            src="https://raw.githubusercontent.com/raviprakash11/pics/master/rp-2.png"
            alt="logo"
            style={{ width: "auto", height: "24px" }}
            className="image-logo"
          />
        </div>
        <div>
          <Button
            id="basic-button"
            aria-controls={open ? "basic-menu" : undefined}
            aria-haspopup="true"
            aria-expanded={open ? "true" : undefined}
            onClick={handleClick}
          >
            <WidgetsIcon className="menu-icon" />
          </Button>
          <Menu
            id="basic-menu"
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}
            MenuListProps={{
              "aria-labelledby": "basic-button",
            }}
          >
            <MenuItem onClick={handleClose}>About Me</MenuItem>
            <MenuItem onClick={handleClose}>Services</MenuItem>
            <MenuItem onClick={handleClose}>Project</MenuItem>
            <MenuItem onClick={handleClose}>Blog</MenuItem>
          </Menu>
        </div>
      </div>
      <div className="avatar">
        <div>
          <Avatar
            alt="Ravi Prakash"
            src="https://raw.githubusercontent.com/raviprakash11/pics/master/profile-pic12.png"
            sx={{ height: "100px", width: "100px", objectFit: "cover" }}
          />
        </div>
      </div>
      <div className="name-heading">
        <div style={{ fontSize: "4.2vh" }}>Ravi P</div>
        <div style={{ fontSize: "3.2vh", paddingTop: "1vh" }}>UI Developer</div>

        <BTNComp/>

        <div className="card-container">
          <Card className="card-design">
            <div>
              <AutorenewIcon className="card-icon" />
            </div>
            <div className="project-count">12</div>
            <div className="card-subtext">In Progress</div>
          </Card>

          <Card className="card-design">
            <div>
              <QueryBuilderIcon className="card-icon" />
            </div>
            <div className="project-count">03</div>
            <div className="card-subtext">waiting</div>
          </Card>
        </div>

        <div className="subtext-container">
          <div>Available Now</div>
          <div>. $20 hr</div>
        </div>

        <div className="hire-me-btn">
          <div>Hire Now</div>
        </div>
      </div>
    </div>
  );
}
